import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listLivestreamVideo } from '../../../api/livestreams';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const LivestreamListView = () => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [videos, setContents] = useState([]);

  const getVideos = useCallback(async () => {
    try {
      const videos = await listLivestreamVideo();
      if (isMountedRef.current) {
        setContents(videos);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getVideos();}, [getVideos]);

  return (
    <Page
      className={classes.root}
      title="Trường học"
    >
      <Container maxWidth={false}>
        <Header/>
        <Box mt={3}>
          <Results contents={videos}/>
        </Box>
      </Container>
    </Page>
  );
};

export default LivestreamListView;
